.FileUploader {
  width: 100%;
}
.FileUploader {
  .dropzone {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-width: 2px;
    border-radius: 2px;
    border-color: #eeeeee;
    border-style: dashed;
    background-color: #fafafa;
    color: #bdbdbd;
    outline: none;
    transition: border 0.24s ease-in-out;
  }
}
.downloadTemplate span {
  cursor: pointer;
  color: var(--emptyTextField);
}
.downloadTemplate span:hover {
  cursor: pointer;
  color: var(--primaryGrey);
}
